import * as React from 'react';
import { connect } from 'react-redux';
import utils from '../../../../utils/client';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Input, Label, Row, Col, InputGroup, InputGroupAddon } from 'reactstrap';
import { closeModal } from '../../actions';
import axios from 'axios';

type Props = {
  closeModal: () => void,
  activeModalClassName: string,
  className: string,
  admin: string,
}
type States = {
  backdrop: boolean,
  isResetReady: boolean
}
class ResetModal extends React.Component<Props, States> {
  constructor(props) {
    super(props);
    this.state = {
      backdrop: true,
      isResetReady: false
    };
    this.close = this.close.bind(this);
    this.handleResetInput = this.handleResetInput.bind(this);
    this.handleResetBtn = this.handleResetBtn.bind(this);
  }
  pointInputFields = [];

  close() {
    this.props.closeModal();
  }

  handleResetInput(e) {
    let val = e.currentTarget.value;
    if ( val == 'reset' ) {
      this.setState({
        isResetReady: true
      });
    } else {
      this.setState({
        isResetReady: false
      });
    }
  }

  async handleResetBtn(e) {
    if ( this.state.isResetReady ) {
      const config = {
        method: 'POST',
        url: '/admin/reset',
        data: {
          resetPassword: 'discovery_reset'
        }
      };
      utils.simpleAxios(axios, config).then(() => {
        alert( "성공 !" );
        window.location.reload(); // refresh
      });
    } else {
      alert( "ERROR : 다시 확인해 주세요" );
    }
  }

  handleResetWordsToNull = async (e) => {
    const config = {
      method: 'POST',
      url: '/admin/words-reset/null',
      data: {}
    };
    utils.simpleAxios(axios, config).then(() => {
      alert( "성공 !" );
    });
  }

  handleResetSpeedToZero = async (e) => {
    const config = {
      method: 'POST',
      url: '/admin/speeds-reset/zero',
      data: {}
    };
    utils.simpleAxios(axios, config).then(() => {
      alert( "성공 !" );
    });
  }

  render() {
    return (
      <Modal isOpen={ (this.props.activeModalClassName == this.props.className) ? true : false } toggle={this.close} className={this.props.className} size="sm">
        <ModalHeader toggle={this.close}>
          <span>초기화</span>
        </ModalHeader>
        <ModalBody>
          <Row>
            <Col>
              <Button className="mb-10" color="danger" onClick={this.handleResetWordsToNull}>명령어 전체 삭제</Button>
            </Col>
          </Row>
          <Row>
            <Col>
              <Button className="mb-30" color="warning" onClick={this.handleResetSpeedToZero}>속도값 0으로 초기화</Button>
            </Col>
          </Row>
          <Row>
            <Col className="notice-before-reset">
              주의 : 보이스/리모콘은 관리자 기본값으로 설정되고 포크봇 비밀번호는 모두 0으로 초기화됩니다.
            </Col>
          </Row>
          <Row>
            <Col>
              <Label>
                아래에 reset을 입력하세요
              </Label>
              <InputGroup>
                <Input placeholder="reset" onChange={this.handleResetInput}></Input>
                <InputGroupAddon addonType="append"><Button color="primary" onClick={this.handleResetBtn}>확인</Button></InputGroupAddon>
              </InputGroup>
            </Col>
          </Row>
        </ModalBody>
        <ModalFooter>
        </ModalFooter>
      </Modal>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    activeModalClassName : state.modalControl.activeModalClassName,
  };
}

export default connect(mapStateToProps, { closeModal })(ResetModal);